
.content{
  margin-top: -20px;
  width: 100%;
  display: inline-block;
}
.schedule{
  position: relative;
  width: 94%;
  margin-left: 3%;
  margin-bottom: 20px;
  display: inline-block;
}
.schedule-header{
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  background-color: #3a1a8b;
  color: #46aecd;
  letter-spacing:2px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
.schedule-header span{
  color: #fff;
}
.schedule-content{
  padding: 8px 6px;
  background-color: #fff;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
.notice{
  font-size: 14px;
  line-height: 22px;
  text-indent: 2em;
}
.notice > span{
  color: red;
  font-weight: bold;
  letter-spacing:2px;
  padding: 0 4px;
}
/*课程表*/
.course{
  width: 100%;
  display: inline-block;
  font-size: 12px;
}
.course li{
  border: 1px solid #dcdcdc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.course li:not(:last-child){
  border-bottom: 0;
}
.course li span.name{
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50px;
          flex: 0 0 50px;
  text-align: center;
  border-right: 1px solid #dcdcdc;
  font-weight: 700;
  line-height: 76px;
}
.course li span.text{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 8px 10px;
  line-height: 20px;
}
/*end*/
.familyTable{
  width: 100%;
}
.familyTable thead tr th,.familyTable tbody tr td{
  border: 1px solid #dcdcdc;
  padding: 10px 5px;
  position: relative;
  font-size: 14px;
  text-align: center;
}
.familyTable thead tr th div{
  position: absolute;
  width: 78px;
  height: 1px;
  background-color: #dcdcdc;
  top: 16px;
  left: -3px;
  -webkit-transform: rotate(25deg);
          transform: rotate(25deg);
}
.familyTable thead tr th:nth-child(1){
  width: 58px;
}
.familyTable thead tr th, .familyTable tbody tr td:first-child{
  font-weight: 700;
}
.familyTable thead tr th span.one{
  position: absolute;
  right: 4px;
  top: 4px;
  font-size: 10px;
}
.familyTable thead tr th span.two{
  position: absolute;
  left: 4px;
  bottom: 4px;
  font-size: 10px;
}
.bonus{
  width: 100%;
  display: inline-block;
  line-height: 22px;
}
.bonus h1{
  font-weight: 700;
  margin: 8px 0 0 0;
  font-size: 14px;
}
.bonus p{
  font-size: 14px;
}
.activity{
  text-align: center;
  font-size: 14px;
  text-decoration: underline;
  color: #fff;
  -webkit-animation: bounce-in 4s linear 2s infinite alternate;
          animation: bounce-in 4s linear 2s infinite alternate;
}
.activity span{
  color: red;
}
.activity span.pay{
  display: inline-block;
  padding: 0 8px;
  font-size: 16px;
  font-weight: 700;
}
.study{
  width: 100%;
  display: inline-block;
  padding: 2px 0;
}
.study li{
  width: 100%;
  height: 40px;
  display: inline-block;
  background-color: #3810c4;
  border-radius: 40px;
  margin-bottom: 10px;
  font-size: 14px;
  color: #fff;
}
.study li:last-child{
  margin-bottom: 0;
}
.study li span{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: inline-block;
  background-color: #fff;
  color: #3810c4;
  margin: 10px;
  text-align: center;
  line-height: 20px;
  vertical-align: center;
}
@media only screen and (min-width: 300px) and (max-width: 320px) {
.familyTable thead tr th div{
    width: 69px;
    top: 16px;
    left: -3px;
    -webkit-transform: rotate(29deg);
            transform: rotate(29deg);
}
.familyTable thead tr th:nth-child(1){
    width: 48px;
}
.familyTable thead tr th,.familyTable tbody tr td{
    font-size: 10px;
}
.bonus p, .study li{
    font-size: 12px;
}
}
@media only screen and (min-width: 410px) and (max-width: 420px) {
.familyTable thead tr th div{
    width: 88px;
    top: 16px;
    left: -3px;
    -webkit-transform: rotate(22deg);
            transform: rotate(22deg);
}
.familyTable thead tr th:nth-child(1){
    width: 69px;
}
.familyTable thead tr th,.familyTable tbody tr td{
    font-size: 14px;
}
.bonus p{
    font-size: 14px;
}
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}

